

<template>
  <div>
    <h1 class="text-4xl font-bold mb-4">Task List</h1>
    <input type="text" v-model="newTask" class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" />
    <button @click="addTask" >Add Task</button>
    <ul>
      <li v-for="task in tasks" :key="task.id">
        <input type="checkbox" v-model="task.completed" />
        {{ task.name }}
        <button @click="startEditing(task.id)">Edit</button>
        <button @click="deleteTask(task.id)">Delete</button>

        <!-- Edit form -->
        <div v-if="editingTaskId === task.id">
          <label>Title:</label>
          <input v-model="editedTaskName" />
          <label>Description:</label>
          <textarea v-model="editedTaskDescription"></textarea>
          <button @click="finishEditing">Save Changes</button>
        </div>
      </li>
    </ul>
  </div>
</template>
<style>
  @import '@/assets/css/tailwind.css'; 
</style>
<script>


export default {
  data() {
    return {
      tasks: [],
      newTask: '',
      editingTaskId: null,
      editedTaskName: '',
      editedTaskDescription: '',
    };
  },
  methods: {
    addTask() {
      this.tasks.push({
        id: Math.random().toString(36).substring(7),
        name: this.newTask,
        completed: false,
      });
      this.newTask = '';
    },
    deleteTask(id) {
      this.tasks = this.tasks.filter((task) => task.id !== id);
    },
    startEditing(id) {
      this.editingTaskId = id;
      const editedTask = this.tasks.find((task) => task.id === id);
      this.editedTaskName = editedTask.name;
      this.editedTaskDescription = editedTask.description || ''; // Add description property if not present
    },
    finishEditing() {
      const editedTask = this.tasks.find((task) => task.id === this.editingTaskId);
      editedTask.name = this.editedTaskName;
      editedTask.description = this.editedTaskDescription;
      this.editingTaskId = null;
      this.editedTaskName = '';
      this.editedTaskDescription = '';
    },
  },
};
</script>



